import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter);

const router = new VueRouter({
  mode: 'history',
  //所有路由
  routes:[
    {
      path: '/user',
      component: () => import('./../pages/user'),
      children: [
        {path: 'mine', component: () => import('../pages/user/mine/Mine')},
        {path: 'login', component: () => import('../pages/user/login/Login')},
        {path: 'myWorkBook', component: () => import('../pages/user/myWorkBook/myWorkBook')},
        {path: 'home', component: () => import('../pages/user/home/Home')},
        {path: 'workbookList', component: () => import('../pages/user/workbookList/workbookList')},
        {path: 'setting', component: () => import('../pages/user/setting/Setting')},
        {path: 'myCollect', component: () => import('../pages/user/myCollect/myCollect')},
        {path: 'question', component: () => import('../pages/user/question/question')},
        {path: 'myRecord', component: () => import('../pages/user/myRecord/myRecord')},
        {path: 'workbookDetail/:workbookId', component: () => import('../pages/user/workbookDetail/workbookDetail')},
        {path: '/user',redirect: '/user/home'},
      ]
    },
    {
      path: '/admin',
      component: () => import('./../pages/admin'),
      children: [
        {path: 'mine', component: () => import('../pages/admin/mine/Mine')},
        {path: 'login', component: () => import('../pages/admin/login/Login')},
        {path: 'userManage', component: () => import('../pages/admin/userManage/userManage')},
        {path: 'workBookManage', component: () => import('../pages/admin/WorkBookManage/WorkBookManage')},
        {path: 'adminManage', component: () => import('../pages/admin/adminManage/adminManage')},
        {path: 'answerManage', component: () => import('../pages/admin/answerManage/answerManage')},
        {path: '/admin',redirect: '/admin/mine'},
      ]
    },
    {//默认路由
      path: '/',
      redirect: '/user'
    }
  ]
});
export default router;
