import {ADD_USER_INFO, DEL_USER_INFO, ADD_ADMIN_INFO, DEL_ADMIN_INFO} from './mutation-types'
export default {
  [ADD_USER_INFO](state, {userInfo}){
    state.userInfo = userInfo;
  },
  [DEL_USER_INFO](state){
    state.userInfo = {};
  },
  [ADD_ADMIN_INFO](state, {adminInfo}){
    state.adminInfo = adminInfo;
  },
  [DEL_ADMIN_INFO](state){
    state.adminInfo = {};
  },
}
